.login{
    display:grid;
    place-items: center;
    margin-left:auto;
    margin-right:auto;
    padding-top:10px;
    padding-bottom: 100px;
}
.login > img{
    object-fit:contain;
    height:100px;
    margin-top: 10px;
    margin-bottom:20px;
}
.login > form{
     display:flex;
     flex-direction: column;
}
.login > form >input{
    width:350px;
    height:50px;
    font-size:20px;
    padding-left:10px;
    border-radius:5px;
    margin-top: 10px;

}
.login > form >button{
    width:365px;
    height:50px;
    font-size:large;
     color: #fff;
     background-color: #001c3d;
     margin-top: 10px;
    
}
.login__register{
    color: #0177b7;
    cursor:pointer;
}
.login >p{
    margin-top:20px;

}